@import "../../../src/common/styles/sass/variables";

.work {
    width: 450px;
    background-color: $second-color;
    align-items: flex-start;
    .workImage {
        display: flex;
        height: 300px;
        width: 100%;
        justify-content: center;
        align-items: center;
        .workImg {
            height: 100%;
            width: 100%;
            position: relative;
        }
        .button {
            position: absolute;
        }
    }
    .workDescription {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100px;
        width: 100%;
        //border: solid green 1px;

        flex-wrap:wrap;
        align-items: flex-start;
        //align-content: space-around;
        position: relative;

        .descriptionTitle {
            font-size: 24px;
            font-weight: 600;
            line-height: 1.3;
            color: #000;
            font-family: $font-extra;
            margin-top: 13px;
            margin-left: 20px;
            position: relative;
            ::before     {
                content: '';
                position: absolute;
                width: 50px;
                height: 1.3px;
                background: #deb99e;
                bottom: -2px;
                right: 62%;
                //transform: translate(-50%, 50%)
                z-index: 1;
            }
        }

        .description {
            display: inline-block;
            font-family: $font-extra;
            font-weight: 700;
            color: $extra-color;
            text-transform: uppercase;
            margin-bottom: 15px;
            margin-left: 20px;
        }
    }
}










